/* -------------------SIGNIN STYLES-------------------- */

.main-signin {
  padding: 5% 10%;
  min-height: 95vh;
  @include gradient-background();

  svg {
    fill: $font-color-dark;
    height: 6rem;
    width: 6rem;
  }

  .form-signin {
    width: 100%;
    max-width: 20rem;
    padding: 1rem;
    margin: auto;
    background-color: $clr-white;
    h2 {
      color: $font-color-dark;
    }

    .form-control {
      margin: 0;
      position: relative;
      box-sizing: border-box;
      height: auto;
      padding: 10px;
      font-size: 1rem;
    }

    .form-control:focus {
      z-index: 2;
    }

    input[type='email'] {
      margin-bottom: -1px;
      border-bottom-right-radius: 0;
      border-bottom-left-radius: 0;
    }

    input[type='password'] {
      margin-bottom: 10px;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }
  }

  @media only screen and (max-width: 575px) {
    padding: 5% 3%;
  }
}
